/**
 * General - https://wordpress.org/documentation/article/css/
 */

@use "../reset/variables" as variables;

.page-content,
.comments-area {
	margin-bottom: 3rem;
	a {
		text-decoration: underline;
	}
}

.alignright {
	float: right;
	margin-left: 1rem;
}

.alignleft {
	float: left;
	margin-right: 1rem;
}

.aligncenter {
	clear: both;
	display: block;
	margin-inline: auto;
}

//Gutenberg support
.alignwide {
	margin-inline: -80px;
}

//Gutenberg support
.alignfull {
	margin-inline: calc(50% - 50vw);
	max-width: 100vw;
	width: 100vw;

	img  {
		width: 100vw;
	}
}

.wp-caption {
	margin-block-end: 1.25rem;
	max-width: 100%;

	&.alignleft {
		margin: 5px 20px 20px 0;
	}

	&.alignright {
		margin: 5px 0 20px 20px;
	}

	img {
		display: block;
		margin-inline: auto;
	}
}

.wp-caption-text {
	margin: 0;
}

.gallery-caption {
	display: block;
	font-size: variables.$sm-font-size;
	line-height: variables.$line-height-base;
	margin: 0;
	padding: variables.$xs-padding;
}

.pagination {
	display: flex;
	justify-content: space-between;
	margin: 20px auto;
}

.sticky {
	position: relative;
	display: block;
}

.bypostauthor {
	font-size: inherit;
}

.hide {
    display: none !important;
}

.post-password-form {
	max-width: 500px;
	margin: 50px auto;

	p {
		width: 100%;
		display: flex;
		align-items: flex-end;
	}

	[type=submit] {
		margin-inline-start: 3px;
	}
}
.creator-mode-to-top{
	color: #7D8B97
}
.wp-block-table {

	td{
		border: 1px solid #ccc;
		padding: 1.5em;
	}
}